.div-layer-view {
	position: absolute;
	top: 20px;
	right: 15px;
	z-index: 10000;
	min-width: 10px;
	max-width: 95%;
	margin-left: 15px;
	padding: 12px;
	border-radius: 6px;
	background: white;
	font-size: 80%;
}

.div-layer-view-2 {
	position: absolute;
	top: 20px;
	left: 15px;
	z-index: 10000;
	min-width: calc(min(40%, 300px));
	max-width: 95%;
	margin-left: 15px;
	padding: 12px;
	border-radius: 6px;
	background: white;
	font-size: 80%;
}

.div-layer-title {
	padding-bottom: 6px;
	color: gray;
	font-weight: bold;
	font-style: normal;
	font-size: 120%;
}

.div-layer-menu {
	margin: 3px;
	padding: 3px;
}

.div-common-layer-header {
	padding-top: 6px;
	color: lightgray;
	font-size: 80%;
}

.div-layer-source {
	padding-bottom: 6px;
	color: lightgray;
	font-size: 80%;
}

.a-layer-source-link {
	color: lightgray;
	text-decoration: none;
}

.a-layer-source-link:hover {
	color: black;
}
